export const indexToPoint = (index: number): [number, number] => [~~(index / 18), index % 18]
export const generateTileIndexes = (dailySeed: number): [number, number, number, number] => {
  const val1 = ((dailySeed >>> 24) & 0xff) % 0x84
  const val2 = ((dailySeed >>> 16) & 0xff) % 0x84
  const val3 = ((dailySeed >>> 8) & 0xff) % 0x84
  const val4 = (dailySeed & 0xff) % 0x84
  return [indexMap[0][val1], indexMap[1][val2], indexMap[2][val3], indexMap[3][val4]]
}
